
import Box from 'vux/src/components/box/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
    name: 'setPassword',
    components: {
        Box, Cell, XInput, XHeader, Checklist, XButton, Group
    },
    data () {
        return {
            form: {
                password:'',
                nickName:''
            },
            complete: false,
            errMsg: ''
        }
    },
    watch: {
        form: {
            handler () {
                var reg = /^[a-zA-Z\d]{8,10}$/;
                this.complete = reg.test(this.form.password) && this.form.nickName;
                if(reg.test(this.form.password)){
                    this.errMsg = '';
                }
            },
            deep: true
        }
    },
    methods: {
        checkFrom (){
            var eleReg = /^[a-zA-Z\d]+$/;
            var reg = /^[a-zA-Z\d]{8,10}$/;
            if(!this.form.password){
                this.errMsg = '密码不能为空'
            }else if(!eleReg.test(this.form.password)){
                this.errMsg = '密码只能由字母和数字组成，不能包含特殊字符'
            }else if(!reg.test(this.form.password)){
                this.errMsg = '密码的长度为8到10位'
            }else {
                this.errMsg = ''
            }
        },
        handleClick () {
            this.$router.push('/createStore')
        }
    }
}



