
import Box from 'vux/src/components/box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
    name: 'signIn',
    components: {
        Box, XInput, XHeader, Group, XButton, Cell, Flexbox, FlexboxItem
    },
    data () {
        return {
            headerImg: 'static/img/slide_01.png',
            account: '',
            password: '',
            bindWX:false
        }
    },
    methods: {
        checkForm (){
            if(!this.account){
                this.showInfo('请输入帐号');
                return false
            }else if(!this.password){
                this.showInfo('请输入密码');
                return false
            }else {
                return true
            }
        },
        // 登录
        handleSignIn () {
            if(this.checkForm()){
                this.loading();
                let self = this;
                // TODO:帐号密码MD5加密处理

                this.$http.get('static/data/userJson.json').then( response => {
                    if(response.data.errcode == 0){
                        this.showInfo('登录成功', function () {
                            self.$router.push('/')
                        })
                    }
                }, response => {
                    console.log(response)
                })
            }
        },
        // 注册
        handleSignUp () {
            this.$router.push('/signUp')
        },
        clickMore () {

        }
    }
}



