
import Box from 'vux/src/components/box/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
    name: 'signUp',
    components: {
     Box, Cell, XInput, Checklist, XButton, XHeader, Group
    },
    data () {
        return {
            form:{
                tel:'',
                vcode:'',
                hasRead:''
            },
            complete: false,
            showBtn: true,
            number: 60,
        }
    },
    watch: {
        form: {
            handler: function (form) {
                this.complete = form.tel && form.vcode && form.hasRead
            },
            deep: true
        }
    },
    methods: {
        // 获取二维码
        getVcode () {
            var reg = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/;
            if(!reg.test(this.form.tel)){
                this.$vux.toast.show({
                    text: '请输入正确的手机号码',
                    type: 'text'
                })
                return false
            }

            this.showBtn = false;
            var interval = setInterval(function () {
                if(this.number>0){
                    this.number--
                }else {
                    clearInterval(interval);
                    this.showBtn = true;
                    this.number = 60 ;
                }
            }.bind(this),1000)
        },
        // 跳转到下一步
        handleNextStep (){
            this.$router.push('/setPassword')
        }
    }
}



