
import ChinaAddressData from 'vux/src/datas/china_address.json'
import Box from 'vux/src/components/box/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'

export default {
    name: 'createStore',
    components: {
        Box, Checklist, Cell, XInput, XHeader, XButton, Group, XAddress
    },
    data () {
        return {
            addressData: ChinaAddressData,
            form: {
                storeName: '',
                address: [],
                detail: '',
            },
            complete: false
        }
    },
    watch: {
        form: {
            handler: function () {
                this.complete = !!(this.form.storeName && this.form.address.length == 3 && this.form.detail)
            },
            deep: true
        }
    },
    methods: {
        // 创建
        handleCreate () {
            const _this = this;
            this.$vux.toast.show({
                text:'帐号创建成功,即将跳转到登录页面',
                time: '1000',
                onHide (){
                    _this.$router.push('/signIn')
                }
            })

        }
    }
}



