
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import Chart from 'chart.js'

export default {
    name: 'income',
    components: {
        Cell, Group, XButton, XHeader, Flexbox, FlexboxItem
    },
    data () {
        return {

        }
    },
    methods: {
    },
    created () {

        setTimeout(function () {
            initChart();
        },500)
    }
}

function initChart() {
    var ctx = document.getElementById('myChart');
    var myChart = new Chart(ctx, {
        type: 'line',
        data: {
            labels: ['16','17','18','19','20','21','22'],
            datasets: [
                {
                    label: '近7天收入趋势',
                    backgroundColor:'transparent',
                    borderWidth: '1',
                    strokeColor : "rgba(46, 147, 230, 0.92)",
                    borderColor: "rgba(46, 147, 230, 0.92)",
                    pointBackgroundColor : "rgba(46, 147, 230, 0.92)",
                    pointBorderColor : "#fff",
                    data : [65,59,90,81,56,55,40]
                }
            ]
        },
        options: {
            legend: {
                display: true
            },
            scales: {
                xAxes: [{
                    display: true
                }]
            }
        }
    })
}



