
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
    name: 'shouKuang',
    components: {
        Flexbox, FlexboxItem, XHeader
    },
    data () {
        return {
            // 公式
            formula:'',
            total: 0,
            discount:0,
            actual:0,
            // 当前环境 (加/小数)(add/dot)
            environment:'add'
        }
    },
    watch: {
        formula (val){
            var _this = this;
            this.total = 0;
            String(val).split('+').forEach(function (item) {
                _this.total += Number(item);
            })
        }
    },
    methods: {
        // 点击键盘事件
        handlerClickNum (val) {
            this.formula += val;
        },
        // 收款
        handlerCollect (){
            if(Number(this.total)){
                this.$router.push('/SK_QRcode')
            }else {
                this.$vux.toast.show({
                    text: '请输入收款金额',
                    type: 'text'
                })
            }
        },
        // 点击加号
        handlerAdd (){
            var last = this.formula.split('+').pop();
            if(/^[\d\.]+$/.test(last) && last.slice(-1) !== '.'){
                this.formula += '+';
            }
        },
        // 点击删除
        handlerDel (){
            this.formula = this.formula.slice(0, -1);
        },
        // 点击小数点
        handlerClickDot (){
            var last = this.formula.split('+').pop();
            if(/^\d+$/.test(last)){
                this.formula += '.';
            }
        },
        // 点击 00
        handlerClickDouble0 (){
            var last = this.formula.split('+').pop();
            if(last !== '0' && last !== ''){
                this.formula += '00';
            }
        }
    }
}



